  content: '';
}
/*
 * States
 * 
 */
.is-hidden {
  visibility: hidden;
  opacity: 0;
  transition: all .6s ease;
}
.is-visible {
  visibility: visible;
  opacity: 1;
}
/*
 * Animations
 * 
 */
/* Spinner animation */
.animate-spin {
  animation: spin 2s infinite linear;
  display: inline-block;
}
@keyframes spin {
0% {
    transform: rotate(0deg);
}
100% {
    transform: rotate(359deg);
}
