
.site-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
  font-size: 13px;
  padding: 40px 40px 0 40px;
}
.site-header .icon-aspen {
  transition: all .12s ease;
  opacity: .8;
}
.site-header p {
  line-height: 1.3em;
}
.site-header a {
  text-decoration: none;
}
.site-header a:hover .icon-aspen {
  color: #ffcb00;
  opacity: 1;
}
/*
 * Site Header Title
 *
 */
.site-header-title {
  margin-bottom: 30px;
}
.site-header-title h1 {
  font-size: 1.4rem;
  line-height: 120%;
  font-weight: 200;
  margin: 0;
}
.site-header-title h1 span {
  font-size: 2rem;
  line-height: 110%;
  display: block;
}
/*
 * Site Header Nav
 *
 */
.site-header-nav {
  list-style-type: none;
  padding: 0;
  margin: 30px 0;
}
.site-header-nav__item {
  display: inline-block;
  position: relative;
  margin: 0 2px;
}
.site-header-nav__item span + span {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  width: 0;
  height: 0;
  display: block;
}



