
.latest-track {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  line-height: 100%;
  text-align: left;
  text-decoration: none;
  margin: 30px auto 0;
  padding: 5px;
  width: 100%;
  max-width: 250px;
  transition: all .3s ease;
}
.latest-track:hover {
  transform: translate3d(0, -2px, 0);
}
.latest-track__cover img {
  width: 60px;
  height: auto;
  border-radius: 3px;
  transition: all .12s ease;
}
.latest-track__cover--is-spinning img {
  border-radius: 100%;
  animation: spin 7s linear infinite;
}
@keyframes spin {
100% {
    transform: rotate(360deg);
}
}
.latest-track__cover,
.latest-track__info {
  display: table-cell;
  vertical-align: top;
}
.latest-track__info {
  font-size: 11px;
  line-height: 120%;
  padding: 5px 8px;
  color: #787887;
}
.latest-track__name {
  font-weight: bold;
}
.latest-track__name,
.latest-track__artist {
  display: block;
}
.latest-track__date {
  display: block;
  font-size: 9px;
  margin: 5px 0;
}
.latest-track__date:first-child {
  margin-top: 0;
}
.latest-track__date:last-child {
  margin-bottom: 0;
}



